import React from 'react';
import { Panel, PanelHeader, Group, Div, Button, ListItem, Avatar } from '@vkontakte/vkui';

const Home = props => (
    <Panel id={props.id}>
        <PanelHeader>Rachel</PanelHeader>
        
        <Group title="Меню">
            <Div>
                <Button size="xl" level="secondary" onClick={props.go} data-to="info">
                    Информация
                </Button>
            </Div>
        </Group>
    </Panel>
);
export default Home;